import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import "react-datepicker/dist/react-datepicker.css";
import voltar from '../images/voltar.svg';
import axios from 'axios';

function Menu() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewHospitais = useSelector((state) => state.hospitais);
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);
  const idadeusuario = useSelector((state) => state.idadeusuario);
  const conveniousuario = useSelector((state) => state.conveniousuario);

  const [hospitais, sethospitais] = useState([]);
  useEffect(() => {
    if (viewHospitais === 1) {
      // BUCETA: criando lista de hospitais cadastrados no gHealth que atendem o convênio do usuário.
      axios.get(html + "/gethospitais/'" + conveniousuario + "'").then((response) => {
        sethospitais(response.data);
      });
      sethospitais(['LIFE CENTER', 'MATER DEI', 'SANTA RITA']);
    }
  }, [viewHospitais])

  // retornar ao login.
  const back = () => {
    changeState({
      type: 'LOGIN_ON',
      // payloads.
      payloadIdusuario: '',
      payloadNomeusuario: 0,
      payloadConvenio: '',
    })
  }
  function Back() {
    return (
      <div
        style={{
          position: 'absolute',
          top: 10, left: 10,
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'flex-start',
        }}>
        <button
          className="blue-button"
          style={{
            margin: 10,
            borderRadius: 50,
            opacity: 0.8,
            minHeight: 30,
            minWidth: 30,
            height: 30,
            width: 30,
          }}
          onClick={() => back()}
          title="VOLTAR"
        >
          <img
            alt=""
            src={voltar}
            style={{
              margin: 10,
              height: 20,
              width: 20,
            }}
          ></img>
        </button>
        <div className="title2"> {'OLÁ RODRIGO!'}</div>
      </div>
    )
  }

  const selectHospital = (value) => {
    changeState({
      type: 'FLUXO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: nomeusuario,
      payloadIdadeusuario: idadeusuario,
      payloadConvenio: conveniousuario,
      payloadHospital: value,
    })
  }

  // renderização do componente.
  if (viewHospitais === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <Back></Back>
        <div className="columngroup">
          <p className="title1">
            SELECIONE UM HOSPITAL PARA SER ATENDIDO
        </p>
          <div id="menu" className="scroll" style={{ marginTop: 80, maxHeight: '50vh' }}>
            {hospitais.map((item) => (
              <div
                key={item.id}
                className="row"
                onClick={() => selectHospital(item)}
              >
                <button
                  className="blue-button"
                  style={{
                    width: 300,
                  }}
                >
                  {item}
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Menu;
