import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import "react-datepicker/dist/react-datepicker.css";
import MaskedInput from 'react-maskedinput';
import voltar from '../images/voltar.svg';
import Toast from '../components/Toast'

function Cadastro() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // definindo o endereço consumindo-se uma API gratuita (via Cep).
  const [endereco, setendereco] = useState();
  const [bairro, setbairro] = useState();
  const [cidade, setcidade] = useState();
  const [uf, setuf] = useState();
  var cep = '';
  var timeout;
  const setCep = () => {
    clearTimeout(timeout);
    if (cep === '') {
      setendereco('');
      setbairro('');
      setcidade('');
      setuf('');
    }
    timeout = setTimeout(() => {
      cep = document.getElementById("inputCep").value;
      axios.get('https://viacep.com.br/ws/' + cep + '/json/').then((response) => {
        var x = [0, 1];
        x = response.data;
        console.log('ENDEREÇO: ' + x);
        setendereco(x.logradouro);
        setbairro(x.bairro);
        setcidade(x.localidade);
        setuf(x.uf);
      })
    }, 2000);
  }

  const changeState = useDispatch();
  const viewCadastro = useSelector((state) => state.cadastro);

  // carregamento da janela.
  useEffect(() => {
    if (viewCadastro === 1) {
      loadConvenios();
      loadCor();
    }
  }, [viewCadastro]);

  // variáveis associadas ao cadastro.
  var nome = '';
  var social = '';
  var dn = '';
  var mae = '';
  var pai = '';
  var numero = 0;
  var complemento = '';
  var cpf = '';
  var registro = '';
  var rg = '';
  var email = '';
  var orgao = '';
  var carteira = '';

  // retornar ao login.
  const back = () => {
    changeState({
      type: 'LOGIN_ON',
      // payloads.
      payloadIdusuario: '',
      payloadNomeusuario: 0,
      payloadConvenio: '',
    })
  }

  // opções de convênio.
  const [convenios, setconvenios] = useState([]);
  const loadConvenios = () => {
    //axios
    //.get(html + "/convenios")
    //.then((response) => {
    //var x = [0, 1];
    //x = response.data;
    //setconvenios(x);
    //if (x.length < 1) {
    setconvenios(['PARTICULAR', 'UNIMED', 'BRADESCO', 'VITALIS', 'GHAP SAÚDE', 'HAPVIDA'])
    //alert(convenios);
    //}
    //})
  }

  // opções de cor.
  const [cores, setcores] = useState([]);
  const loadCor = () => {
    //axios
    //.get(html + "/convenios")
    //.then((response) => {
    //var x = [0, 1];
    //x = response.data;
    //setconvenios(x);
    //if (x.length < 1) {
    setcores(['NÃO SEI', 'BRANCA', 'PRETA', 'PARDA', 'AMARELA', 'VERMELHA'])
    //alert(convenios);
    //}
    //})
  }

  // exibindo lista de convênios.
  const [showconvenios, setshowconvenios] = useState(0);
  function ShowConvenios() {
    if (showconvenios === 1) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo">
            <div className="scroll" style={{ maxHeight: '50vh' }}>
              <div>
                {convenios.map((item) => (
                  <div
                    key={item.id}
                    className="row"
                    onClick={() => selectConvenio(item)}
                  >
                    <button
                      variant="primary"
                      className="blue-button"
                      style={{
                        width: 300,
                      }}
                    >
                      {item}
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )
    } else {
      return null;
    }
  }

  // exibindo lista de convênios.
  const [showcor, setshowcor] = useState(0);
  function ShowCor() {
    if (showcor === 1) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo">
            <div className="scroll" style={{ maxHeight: '50vh' }}>
              <div>
                {cores.map((item) => (
                  <div
                    key={item.id}
                    className="row"
                    onClick={() => selectCor(item)}
                  >
                    <button
                      variant="primary"
                      className="blue-button"
                      style={{
                        width: 300,
                      }}
                    >
                      {item}
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )
    } else {
      return null;
    }
  }

  // função que exibe a lista de convênios.
  const showConvenios = () => {
    setshowconvenios(1);
  }

  const showCores = () => {
    setshowcor(1);
  }

  // função que define o valor do convênio, após a seleção do mesmo na lista de convênios.
  const [convenio, setconvenio] = useState('');
  const selectConvenio = (value) => {
    setconvenio(value);
    setshowconvenios(0);
  }

  const [cor, setcor] = useState('');
  const selectCor = (value) => {
    setcor(value);
    setshowcor(0);
  }

  // função salva o cadastro de um novo usuário (deverá ser salvo também na tabela de pacientes do gPulse!).
  const insertUsuario = () => {
    document.getElementById("inputNome").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputDn").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputSex").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputCor").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputMae").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputConvenio").style.backgroundColor = '#BFDBF7';
    document.getElementById("inputCarteira").style.backgroundColor = '#BFDBF7';
    nome = document.getElementById('inputNome').value;
    social = document.getElementById('inputSocial').value;
    dn = document.getElementById('inputDn').value;
    mae = document.getElementById('inputMae').value;
    pai = document.getElementById('inputPai').value;
    cep = document.getElementById('inputCep').value;
    numero = document.getElementById('inputNumero').value;
    complemento = document.getElementById('inputComplemento').value;
    cpf = document.getElementById('inputCpf').value;
    registro = document.getElementById('inputRegistro').value;
    email = document.getElementById('inputEmail').value;
    rg = document.getElementById('inputRg').value;
    orgao = document.getElementById('inputOrgao').value;
    carteira = document.getElementById('inputCarteira').value;
    if (nome !== '' && dn !== '' && sex !== 0 && mae !== '' && convenio !== '' && carteira !== '') {
      var obj = {
        nome: nome,
        social: social,
        dn: dn,
        sexo: sex,
        cor: cor,
        mae: mae,
        pai: pai,
        cep: cep,
        endereco: endereco,
        numero: numero,
        complemento: complemento,
        cidade: cidade,
        bairro: bairro,
        uf: uf,
        cpf: cpf,
        registro: registro,
        email: email,
        rg: rg,
        orgao: orgao,
        convenio: convenio,
        carteira: carteira,
      };
      axios.post(html + '/insertusuario', obj);
      setTimeout(() => {
        // BUCETA: criar endpoint que resgata o registro criado, para captura do id, nome e convênio do usuário.
        axios.get(html + "/getusuariocarteira/'" + carteira + "'").then((response) => {
          var x = response.data;
          setTimeout(() => {
            // capturando o primeiro nome do usuário (para exibição).
            var nome = x.nome;
            var firstnamearray = nome.split(' ');
            var firstname = firstnamearray[0];
            changeState({
              type: 'WELCOME_ON',
              // payloads.
              payloadIdusuario: x.id,
              payloadNomeusuario: firstname,
            });
          }, 1000);
        });
      }, 1000);
      toast(1, '#52be80', 'PRÉ-ATENDIMENTO CADASTRADO COM SUCESSO.', 4000);
    } else {
      toast(1, '#ec7063', 'POR FAVOR, PREENCHA OS CAMPOS OBRIGATÓRIOS.', 4000);
      if (nome === '') {
        document.getElementById("inputNome").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
      if (dn === '') {
        document.getElementById("inputDn").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
      if (sex === 0) {
        document.getElementById("inputSex").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
      if (mae === '') {
        document.getElementById("inputMae").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
      if (convenio === '') {
        document.getElementById("inputConvenio").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
      if (carteira === '') {
        document.getElementById("inputCarteira").style.backgroundColor = 'rgba(236, 112, 99, 0.2)';
      }
    }
  };

  // definindo sexo do usuário.
  const [sex, setsex] = useState(0);
  const defineMan = () => {
    setsex(1);
  }
  const defineWoman = () => {
    setsex(2);
  }
  const defineIndeterminated = () => {
    setsex(3);
  }

  function Back() {
    return (
      <div
        style={{
          position: 'absolute',
          top: 10,
          left: 10,
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'flex-start',
        }}>
        <button
          className="blue-button"
          style={{
            margin: 10,
            borderRadius: 50,
            opacity: 0.8,
            minHeight: 30,
            minWidth: 30,
            height: 30,
            width: 30,
          }}
          onClick={() => back()}
          title="VOLTAR"
        >
          <img
            alt=""
            src={voltar}
            style={{
              margin: 10,
              height: 20,
              width: 20,
            }}
          ></img>
        </button>
        <div className="title2"> {'OLÁ RODRIGO!'}</div>
      </div>
    )
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cortoast, setcortoast] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcortoast(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // renderização do componente.
  if (viewCadastro === 1) {
    return (
      <div className="main">
        <Toast valor={valor} cor={cortoast} mensagem={mensagem} tempo={tempo}></Toast>
        <Back></Back>
        <ShowConvenios></ShowConvenios>
        <ShowCor></ShowCor>
        <div id="cadastro" className="columngroup" style={{ marginTop: 50 }}>
          <p className="title1">
            CADASTRO DE USUÁRIO
        </p>
          <div className="columngroup">
            <div className="linegroup">
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">NOME COMPLETO</div>
                <input
                  autoComplete="new-password"
                  placeholder="NOME"
                  className="input"
                  type="text"
                  id="inputNome"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'NOME')}
                  style={{
                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">NOME SOCIAL</div>
                <input
                  autoComplete="new-password"
                  placeholder="NOME SOCIAL"
                  className="input"
                  type="text"
                  id="inputSocial"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'NOME')}
                  style={{
                    width: 150,
                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">DATA DE NASCIMENTO</div>
                <MaskedInput
                  className="input"
                  title="DATA DE NASCIMENTO"
                  placeholder="DATA DE NASCIMENTO"
                  autoComplete="new-password"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'DATA DE NASCIMENTO')}
                  style={{
                    width: 200,
                    height: 50,
                  }}
                  mask="11/11/1111"
                  id="inputDn"
                />
              </div>
            </div>
            <div className="linegroup">
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', marginTop: 10, width: 250 }}>
                <div className="inputcabecalho">SEXO</div>
                <div id="inputSex" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', height: 50, margin: 5, borderRadius: 5 }}>
                  <button
                    className="blue-button"
                    style={{
                      color: '#ffffff',
                      margin: 5,
                      borderRadius: 50,
                      height: 40,
                      width: 40,
                      minHeight: 40,
                      minWidth: 40,
                      opacity: sex === 1 ? 1 : 0.5,
                    }}
                    onClick={() => defineMan()}
                    title="SEXO MASCULINO"
                  >
                    M
                  </button>
                  <button
                    className="red-button"
                    style={{
                      margin: 5,
                      marginLeft: 0,
                      marginRight: 0,
                      color: '#ffffff',
                      borderRadius: 50,
                      height: 40,
                      width: 40,
                      minHeight: 40,
                      minWidth: 40,
                      opacity: sex === 2 ? 1 : 0.5,
                    }}
                    onClick={() => defineWoman()}
                    title="SEXO FEMININO"
                  >
                    F
                  </button>
                  <button
                    className="green-button"
                    style={{
                      color: '#ffffff',
                      margin: 5,
                      borderRadius: 50,
                      height: 40,
                      width: 40,
                      minHeight: 40,
                      minWidth: 40,
                      opacity: sex === 3 ? 1 : 0.5,
                    }}
                    onClick={() => defineIndeterminated()}
                    title="SEXO INDETERMINADO"
                  >
                    I
                  </button>
                </div>
              </div>
              <div className="inputgroup" style={{ width: 250 }}>
                <div className="inputcabecalho">COR</div>
                <div
                  autoComplete="new-password"
                  placeholder="COR"
                  className="input"
                  type="text"
                  id="inputCor"
                  defaultValue={cor}
                  onClick={() => showCores()}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'COR')}
                  style={{
                    height: 50,
                    color: cor ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {cor ? cor : 'COR'}
                </div>
              </div>
            </div>
            <div className="linegroup">
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">NOME DA MÃE</div>
                <input
                  autoComplete="new-password"
                  placeholder="NOME DA MÃE"
                  className="input"
                  type="text"
                  id="inputMae"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'NOME DA MÃE')}
                  style={{

                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">NOME DO PAI</div>
                <input
                  autoComplete="new-password"
                  placeholder="NOME DO PAI"
                  className="input"
                  type="text"
                  id="inputPai"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'NOME DO PAI')}
                  style={{

                    height: 50,
                  }}
                ></input>
              </div>
            </div>
            <div className="linegroup">
              <div className="inputgroup">
                <div className="inputcabecalho">CEP</div>
                <MaskedInput
                  className="input"
                  title="CEP"
                  placeholder="CEP"
                  autoComplete="new-password"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CEP')}
                  onChange={() => setCep()}
                  style={{
                    width: 150,
                    height: 50,
                  }}
                  mask="11111111"
                  id="inputCep"
                />
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">ENDEREÇO</div>
                <div
                  autoComplete="new-password"
                  placeholder="ENDEREÇO"
                  className="input"
                  type="text"
                  id="inputEndereco"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ENDERECO')}
                  style={{
                    height: 50,
                    padding: 10,
                    color: endereco ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {endereco ? endereco : 'ENDEREÇO'}
                </div>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">N°</div>
                <input
                  autoComplete="new-password"
                  placeholder="N°"
                  className="input"
                  type="text"
                  id="inputNumero"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'N°')}
                  style={{
                    width: 50,
                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">COMPLEMENTO</div>
                <input
                  autoComplete="new-password"
                  placeholder="COMPLEMENTO"
                  className="input"
                  type="text"
                  id="inputComplemento"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'COMPLEMENTO')}
                  style={{
                    width: 200,
                    height: 50,
                  }}
                ></input>
              </div>
            </div>
            <div className="linegroup">
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">CIDADE</div>
                <div
                  autoComplete="new-password"
                  placeholder="CIDADE"
                  className="input"
                  type="text"
                  id="inputCidade"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CIDADE')}
                  style={{

                    height: 50,
                    color: cidade ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {cidade ? cidade : 'CIDADE'}
                </div>
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">BAIRRO</div>
                <div
                  autoComplete="new-password"
                  placeholder="BAIRRO"
                  className="input"
                  type="text"
                  id="inputBairro"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'BAIRRO')}
                  style={{

                    height: 50,
                    color: bairro ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {bairro ? bairro : 'BAIRRO'}
                </div>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">UF</div>
                <div
                  autoComplete="new-password"
                  placeholder="UF"
                  className="input"
                  type="text"
                  id="inputUf"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'UF')}
                  style={{
                    width: 50,
                    height: 50,
                    color: uf ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {uf ? uf : 'UF'}
                </div>
              </div>
            </div>
            <div className="linegroup">
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">CPF</div>
                <MaskedInput
                  className="input"
                  title="CPF"
                  placeholder="CPF"
                  autoComplete="new-password"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CPF')}
                  style={{

                    height: 50,
                  }}
                  mask="111.111.111-11"
                  id="inputCpf"
                />
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">REG. DE NASCIMENTO</div>
                <input
                  autoComplete="new-password"
                  placeholder="REG. DE NASCIMENTO"
                  title="PREENCHER EM CASO DE RECÉM-NASCIDO QUE AINDA NÃO POSSUI RG OU CPF."
                  className="input"
                  type="text"
                  id="inputRegistro"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'REG. DE NASCIMENTO')}
                  style={{

                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">RG:</div>
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                  <input
                    autoComplete="new-password"
                    placeholder="RG"
                    className="input"
                    type="text"
                    id="inputRg"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'RG')}
                    style={{

                      height: 50,
                    }}
                  ></input>
                </div>
              </div>
              <div className="inputgroup">
                <div className="inputcabecalho">ÓRGÃO:</div>
                <input
                  autoComplete="new-password"
                  placeholder="ÓRGÃO"
                  className="input"
                  type="text"
                  id="inputOrgao"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ÓRGÃO')}
                  style={{
                    width: 100,
                    height: 50,
                  }}
                ></input>
              </div>
            </div>
            <div className="linegroup">
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">E-MAIL:</div>
                <input
                  autoComplete="new-password"
                  placeholder="E-MAIL"
                  className="input"
                  type="text"
                  id="inputEmail"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'E-MAIL')}
                  style={{

                    height: 50,
                  }}
                ></input>
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">CONVÊNIO:</div>
                <div
                  autoComplete="new-password"
                  placeholder="CONVÊNIO"
                  className="input"
                  type="text"
                  id="inputConvenio"
                  defaultValue={convenio}
                  onClick={() => showConvenios()}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CONVÊNIO')}
                  style={{

                    height: 50,
                    color: convenio ? '#1f7a8c' : 'rgba(31, 122, 140, 0.3)',
                  }}
                >
                  {convenio ? convenio : 'CONVENIO'}
                </div>
              </div>
              <div className="inputgroup" style={{ width: '100%' }}>
                <div className="inputcabecalho">CARTEIRA:</div>
                <input
                  autoComplete="new-password"
                  placeholder="N° DA CARTEIRA"
                  className="input"
                  type="text"
                  id="inputCarteira"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'No. DA CARTEIRA')}
                  style={{

                    height: 50,
                  }}
                ></input>
              </div>
            </div>
            <button
              className="blue-button"
              style={{
                marginTop: 10,
                marginBottom: 10,
                minWidth: 150,
                padding: 10,
                color: '#ffffff',
              }}
              onClick={() => insertUsuario()}
            >
              FINALIZAR CADASTRO
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Cadastro;
