import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import moment, { locale } from 'moment';
import deletar from '../images/deletar.svg';
import 'moment/locale/pt-br';

function Startup() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const viewStartup = useSelector((state) => state.startup);
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);

  var pseudolista = [
    {
      paciente: "RODRIGO CARVALHO LESSA",
      datapedido: '15/12/2020 - 08:00',
      datachegada: '15/12/2020 - 08:45',
      dataatendimento: '',
      convenio: 'UNIMED',
      especialidade: 'CLÍNICA MÉDICA',
      fluxo: 'DOR TORÁCICA',
      cor: 1,
    },
    {
      paciente: "PEDRO MONTEIRO PEREIRA",
      datapedido: '15/12/2020 - 07:00',
      datachegada: '15/12/2020 - 07:35',
      dataatendimento: '',
      convenio: 'BRADESCO',
      especialidade: 'CLÍNICA MÉDICA',
      fluxo: 'FALTA DE AR',
      cor: 2,
    },
  ];

  useEffect(() => {
    if (viewStartup === 1) {
      loadPreCheckins();
      // carregamento periódico da lista de pré-checkins autorizados.
      setInterval(() => {
        loadPreCheckins();
      }, 10000);
      /* LUCAS: apagar a linha de código abaixo (carregando falsa array de pré-chekins autorizados
        para demonstração*/
      setprecheckinsautorizados(pseudolista);
    }
  }, [viewStartup]);

  // carregando a lista de pre-checkins autorizados.
  const [prechekinsautorizados, setprecheckinsautorizados] = useState([]);
  const loadPreCheckins = () => {
    // LUCAS: aqui precisamos executar uma query que selecione registros de pré-checkin autorizados.
    axios.get(html + "/getprecheckinautorizado").then((response) => {
      setprecheckinsautorizados(response.data);
    });
  }

  // modal para confirmação da exclusão de um pedido de pré-checkin.
  const [modaldeleteprecheckin, setmodaldeleteprecheckin] = useState(0);
  function ModalDeletePrecheckin() {
    if (modaldeleteprecheckin === 1) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo" style={{ padding: 30 }}>
            <div className="title2">  EXCLUIR PRÉ-ATENDIMENTO?</div>
            <div className="linegroup" style={{ width: 400, marginTop: 10 }}>
              <button
                className="green-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => setmodaldeleteprecheckin(0)}
              >
                NÃO
            </button>
              <button
                className="red-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => deletePrecheckin(idpreat)}
              >
                SIM
            </button>
            </div>
          </div>
        </div>
      )
    } else {
      return null;
    }

  }

  const selectPrecheckin = (item) => {
    setidpreat(item.paciente);
    setmodaldeleteprecheckin(1);
  }

  const [idpreat, setidpreat] = useState(0);
  const deletePrecheckin = () => {
    // LUCAS: fetch para excluir um registro de pré-checkin.
    axios.get(html + "/deleteprecheckin").then((response) => {
      setTimeout(() => {
        loadPreCheckins();
        setmodaldeleteprecheckin(0);
      }, 1000);
    });
  }

  // renderização do componente.
  if (viewStartup === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <ModalDeletePrecheckin></ModalDeletePrecheckin>
        <div className="columngroup" style={{ textAlign: 'center' }}>
          <div className="title1">
            {'PAINEL DE PRÉ-CHECKINS'}
          </div>
          <div id="cabeçalho" className="scrollheader" style={{ margin: 0 }}>
            <div className="row" style={{ height: 50 }}>
              <button
                className="blue-button"
                style={{
                  width: 300,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                PACIENTE
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                AGENDAMENTO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                CHEGADA
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                CONVÊNIO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                ESPECIALIDADE
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                QUEIXA
              </button>
              <button
                className="red-button"
                style={{
                  width: 30,
                  height: 30,
                  color: '#ffffff',
                  backgroundColor: 'transparent',
                }}
              >
                DEL
                </button>
            </div>
          </div>
          <div id="painel de pré-atendimentos" className="scroll" style={{ marginTop: 10, height: '70vh' }}>
            {pseudolista.map((item) => (
              <div
                key={item.id}
                className="row"
              >
                <button
                  className="blue-button"
                  style={{
                    width: 300,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.paciente}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.datapedido}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.datachegada}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.convenio}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.especialidade}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? '#D4AC0D' : '#52be80',
                  }}
                >
                  {item.fluxo}
                </button>
                <button
                  className="red-button"
                  onClick={() => selectPrecheckin(item)}
                  style={{
                    width: 50,
                  }}
                >
                  <img
                    alt=""
                    src={deletar}
                    style={{
                      margin: 10,
                      height: 30,
                      width: 30,
                    }}
                  ></img>
                </button>
              </div>
            ))}
          </div>
        </div>
      </div >
    );
  } else {
    return null;
  }
}
export default Startup;