import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import "react-datepicker/dist/react-datepicker.css";
import voltar from '../images/voltar.svg';
import axios from 'axios';
import moment, { locale } from 'moment';
import 'moment/locale/pt-br';

function Fluxo() {
  moment.locale('pt-br');
  const changeState = useDispatch();
  const viewFluxo = useSelector((state) => state.fluxo);

  // constantes e variáveis associadas ao registro do usuário.
  const idusuario = useSelector((state) => state.idusuario);
  const nome = useSelector((state) => state.nome);
  const [dn, setdn] = useState('');
  const [convenio, setconvenio] = useState('');
  const [carteira, setcarteira] = useState('');

  // carregando o registro do usuário.
  const loadUsuario = () => {
    axios.get(html + "/usuario/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setdn(x.dn);
      setconvenio(x.convenio);
      setcarteira(x.carteira);
    });
  }

  var html = 'https://pulsarapp-server.herokuapp.com';

  /* LUCAS: as variáveis abaixo (adulto e criança) deverão ser excluídas. Usadas somente para demonstração
  das opções de queixas e sintomas. Estas arrays deverão alimentar uma tabela no banco de dados*/
  var adulto = [
    // CLÍNICA MÉDICA - PRIORIDADE 01:
    { fluxo: "SUOR FRIO, PELE FRIA E PEGAJOSA, PALIDEZ", cor: 1, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "FALTA DE AR, DIFICULDADE PARA RESPIRAR", cor: 1, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "DOR DE CABEÇA FORTE, DESMAIO OU CONVULSÕES.", cor: 1, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "DOR FORTE NO PEITO, PALPITAÇÕES, PULSO LENTO OU ACELERADO", cor: 1, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "GLICOSE BAIXA NO SANGUE", cor: 1, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "VÔMITOS COM SANGUE, TOSSE OU EVACUAÇÃO COM SANGUE. OUTRO SANGRAMENTO IMPORTANTE", cor: 1, especialista: 'CLÍNICA MÉDICA' },

    // CIRURGIA - PIORIDADE 01:
    { fluxo: "FERIMENTO GRAVE COM SANGRAMENTO ATIVO", cor: 1, especialista: 'CIRURGIA' },
    { fluxo: "QUEIMADURA EXTENSA OU EM FACE", cor: 1, especialista: 'CIRURGIA' },
    { fluxo: "PANCADA NA CABEÇA COM PERDA DA CONSCIÊNCIA", cor: 1, especialista: 'CIRURGIA' },

    // CIRURGIA VASCULAR - PRIORIDADE 01:
    { fluxo: "PARTE DO CORPO FRIA, PÁLIDA OU ARROXEADA", cor: 1, especialista: 'CIRURGIA VASCULAR' },

    // GINECOLOGIA E OBSTETRÍCIA - PRIORIDADE 01:
    { fluxo: "TRABALHO DE PARTO", cor: 1, especialista: 'OBSTETRÍCIA' },

    // CLÍNICA MÉDICA - PRIORIDADE 02:
    { fluxo: "DESMAIO RECENTE, PERDA SÚBITA DA FORÇA EM PARTE DO CORPO", cor: 2, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "ABUSO DE ÁLCOOL, MEDICAMENTOS OU OUTRAS DROGAS", cor: 2, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "INCHAÇO NO ROSTO OU NA LÍNGUA", cor: 2, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "GLICOSE ALTA NO SANGUE", cor: 2, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "PICADA DE ESCORPIÃO, COBRA OU OUTRO ANIMAL PEÇONHENTO", cor: 2, especialista: 'PEDIATRIA' },
    { fluxo: "DIARRÉIA COM SANGUE", cor: 2, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "TOSSE COM CHIEIRA", cor: 2, especialista: 'CLÍNICA MÉDICA' },

    // CIRURGIA - PIORIDADE 02:
    { fluxo: "PANCADA NA CABEÇA SEM PERDA DA CONSCIÊNCIA", cor: 2, especialista: 'CIRURGIA' },
    { fluxo: "QUEDA DE GRANDE ALTURA", cor: 2, especialista: 'CIRURGIA' },
    { fluxo: "DOR FORTE NA BARRIGA", cor: 2, especialista: 'CIRURGIA' },
    { fluxo: "DOR FORTE NO ESCROTO", cor: 2, especialista: 'CIRURGIA' },
    { fluxo: "CORTE COM SANGRAMENTO MODERADO", cor: 2, especialista: 'CIRURGIA' },

    // ORTOPEDIA - PRIORIDADE 02:
    { fluxo: "DEFORMIDADE EM PARTE DO CORPO APÓS PANCADA", cor: 2, especialista: 'ORTOPEDIA' },

    // GINECOLOGIA E OBSTETRÍCIA - PRIORIDADE 02:
    { fluxo: "SANGRAMENTO VAGINAL INTENSO", cor: 2, especialista: 'GINECOLOGIA' },

    // CLÍNICA MÉDICA - PRIORIDADE 03:
    { fluxo: "INCHAÇO, COCEIRA, BOLHAS OU VERMELHIDÃO NO CORPO", cor: 3, especialista: 'MÉDICA' },
    { fluxo: "NÁUSEAS OU VÔMITOS", cor: 3, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "DIARRÉIA SEM SANGUE", cor: 3, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "TOSSE SEM CHIEIRA", cor: 3, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "FEBRE", cor: 3, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "TONTEIRA, VERTIGEM OU DOR DE OUVIDO", cor: 3, especialista: 'CLÍNICA MÉDICA' },
    { fluxo: "DOR EM OUTRA PARTE DO CORPO", cor: 3, especialista: 'CLÍNICA MÉDICA' },

    // CIRURGIA - PIORIDADE 03:
    { fluxo: "FERIDA DOLOROSA, COM SECREÇÃO OU MAU CHEIRO", cor: 3, especialista: 'CIRURGIA' },
    { fluxo: "CORTE COM SANGRAMENTO LEVE", cor: 3, especialista: 'CIRURGIA' },

    // UROLOGIA - PRIORIDADE 03:
    { fluxo: "NÃO CONSIGO URINAR, URINA PRESA", cor: 3, especialista: 'CIRURGIA' },

    // PSIQUIATRIA:
    { fluxo: "AGRESSIVIDADE, HISTÓRIA PSIQUIÁTRICA IMPORTANTE", cor: 2, especialista: 'PSIQUIATRIA' },
  ];

  var crianca = [
    { fluxo: "CRIANÇA SONOLENTA OU PROSTRADA", cor: 1, especialista: 'PEDIATRIA' },
    { fluxo: "SUOR FRIO, PELE FRIA E PEGAJOSA, PALIDEZ", cor: 1, especialista: 'PEDIATRIA' },
    { fluxo: "DOR DE CABEÇA INTENSA, DESMAIO OU CONVULSÕES", cor: 1, especialista: 'PEDIATRIA' },
    { fluxo: "PICADA DE ESCORPIÃO, COBRA OU OUTRO ANIMAL PEÇONHENTO", cor: 1, especialista: 'PEDIATRIA' },

    { fluxo: "CRIANÇA INCONSOLÁVEL, CHORO ININTERRUPTO, CRIANÇA INCAPAZ DE SE ALIMENTAR", cor: 2, especialista: 'PEDIATRIA' },
    { fluxo: "CRIANÇA MUITO FEBRIL COM MANCHAS NO CORPO", cor: 2, especialista: 'PEDIATRIA' },
    { fluxo: "CRIANÇA SEM URINAR, COM BOCA SECA", cor: 2, especialista: 'PEDIATRIA' },

    { fluxo: "CRIANÇA FEBRIL", cor: 3, especialista: 'PEDIATRIA' },
    { fluxo: "TOSSE OU CHIEIRA", cor: 3, especialista: 'PEDIATRIA' },

  ];

  useEffect(() => {
    if (viewFluxo === 1) {
      loadUsuario();
      /* LUCAS: a função abaixo carrega a tabela com as opções de queixas e sintomas, conforme a
      idade do usuário */
      defineFluxo();
      setfluxo(adulto); // LUCAS: essa linha deve ser retirada. Está carregando a array adulto para demonstração.
    }
  }, [viewFluxo])

  // definindo as listas de queixas e sintomas.
  const [fluxo, setfluxo] = useState([]);
  const loadFluxoCrianca = () => {
    axios.get(html + "/fluxocrianca").then((response) => {
      setfluxo(response.data);
    });
  }
  const loadFluxoAdulto = () => {
    axios.get(html + "/fluxoadulto").then((response) => {
      setfluxo(response.data);
    });
  }

  // definindo a lista de queixas e sintomas a ser empregada conforme a idade do usuário.
  var age = moment().diff(moment(dn, 'DD/MM/YYYY'), 'years');
  const defineFluxo = () => {
    if (age < 14) {
      loadFluxoCrianca();
    } else {
      loadFluxoAdulto();
    }
  }

  // retornar ao login (botão voltar).
  const back = () => {
    changeState({
      type: 'LOGIN_ON',
      // payloads.
      payloadIdusuario: '',
      payloadNome: '',
    })
  }
  function Back() {
    return (
      <div
        style={{
          position: 'absolute',
          top: 10, left: 10,
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'flex-start',
        }}>
        <button
          className="blue-button"
          style={{
            margin: 10,
            borderRadius: 50,
            opacity: 0.8,
            minHeight: 30,
            minWidth: 30,
            height: 30,
            width: 30,
          }}
          onClick={() => back()}
          title="VOLTAR"
        >
          <img
            alt=""
            src={voltar}
            style={{
              margin: 10,
              height: 20,
              width: 20,
            }}
          ></img>
        </button>
        <div className="title2"> {'OLÁ RODRIGO!'}</div>
      </div>
    )
  }

  // salvando um registro de pré-checkin ao selecionarmos uma queixa ou sintoma da lista.
  var horapedido = moment().format('DD/MM/YYYY hh:mm:ss'); // momento em que o usuário solicitou o pré-checkin.
  const selectFluxo = (item) => {
    var obj = {
      idusuario: idusuario,
      nome: nome,
      convenio: convenio,
      carteira: carteira,
      fluxo: item.fluxo,
      escalador: 0,
      cor: item.cor,
      especialista: item.especialista,
      horapedido: horapedido,
      horachegada: '', // momento em que o usuário informa que chegou no hospital para ser atendido.
      chegou: 0, // 0 = não chegou para atendimento no hospital, 1 = chegou no hospital.
      autorizado: 0 // 0 = não autorizado, 1 = autorizado.
    };
    axios.post(html + "/insertpretriagem", obj);
    // abrindo a tela "sucesso".
    changeState({
      type: 'SUCESSO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNome: nome,
    });
    // LUCAS: apagar todo o bloco abaixo (usado para demonstração do fluxo das telas)!
    changeState({
      type: 'SUCESSO_ON',
      // payloads.
      payloadIdusuario: 1,
      payloadNome: nome,
    });
  }

  // componente e funções que tratam do filtro para busca de queixas ou sintomas.
  function ShowFilter() {
    return (
      <div>
        <input
          className="input"
          autoComplete="off"
          placeholder="BUSCAR QUEIXA OU SINTOMA..."
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'BUSCAR QUEIXA OU SINTOMA...')}
          onChange={() => filterQueixa()}
          style={{
            width: 300,
            marginLeft: 0,
          }}
          type="text"
          id="inputFilter"
          defaultValue={filterqueixa}
          maxLength={100}
        ></input>
      </div>
    )
  }
  const [filterqueixa, setfilterqueixa] = useState('');
  var searchqueixa = '';
  const [arrayfluxos, setarrayfluxos] = useState([]);
  const filterQueixa = () => {
    clearTimeout(timeout);
    document.getElementById("inputFilter").focus();
    searchqueixa = document.getElementById("inputFilter").value.toUpperCase();
    var timeout = setTimeout(() => {
      if (searchqueixa === '') {
        setdorscale(0);
        setarrayfluxos([]);
        document.getElementById("inputFilter").value = '';
        document.getElementById("inputFilter").focus();
      } else {
        setdorscale(0);
        setfilterqueixa(document.getElementById("inputFilter").value.toUpperCase());
        setarrayfluxos(adulto.filter(item => item.fluxo.includes(searchqueixa) === true));
        if (arrayfluxos.length > 0) {
          document.getElementById("fluxos").style.visibility = 'block';
          document.getElementById("fluxos").style.height = '50vh';
        }
        document.getElementById("inputFilter").value = searchqueixa;
        document.getElementById("inputFilter").focus();
      }
    }, 1000);
  }

  // componente e funções que tratam da escala de dor.
  const [dorscale, setdorscale] = useState();
  const scalaDor = () => {
    document.getElementById("fluxos").style.visibility = 'hidden';
    document.getElementById("fluxos").style.height = '0vh';
    setdorscale(1);
  }
  const [dor, setdor] = useState(0);
  const selectDor = (value) => {
    setdor(value);
    /* definindo a cor do fluxo dor, conforme a escala analógica setada pelo usuário:
    0 a 3 = dor leve (verde), 4 a 7 = dor moderada (amarela), > 7 = dor intensa. */
    var cor = 0;
    if (value < 4) {
      cor = 3;
    } else if (cor > 3 && cor < 8) {
      cor = 2;
    } else {
      cor = 1;
    }
    // salvando o registro de pré-checkin.
    var obj = {
      idusuario: idusuario,
      nome: nome,
      convenio: convenio,
      carteira: carteira,
      fluxo: 'DOR',
      escalador: dor,
      cor: cor,
      especialista: 'CLÍNICA MÉDICA',
      horapedido: horapedido,
      horachegada: '', // momento em que o usuário informa que chegou no hospital para ser atendido.
      chegou: 0, // 0 = não chegou para atendimento no hospital, 1 == chegou no hospital.
      autorizado: 0 // 0 = não autorizado, 1 = autorizado.
    };
    axios.post(html + "/insertpretriagem", obj);
    changeState({
      type: 'SUCESSO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: nome,
    });
  }
  function ShowEscalaDor() {
    if (dorscale === 1) {
      return (
        <div className="title2"
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            textAlign: 'center',
            width: 350,
          }}>
          <p>
            EM UMA ESCALA DE 0 A 10, SENDO 10 A DOR MAIS INTENSA QUE VOCÊ JÁ SENTIU,
            COMO VOCÊ CLASSIFICARIA ESSA DOR?
          </p>
          <div className="linegroup" style={{ flexWrap: 'wrap', flexDirection: 'row' }}>
            <button
              onClick={() => selectDor(1)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 1 ? 0.5 : 1,
              }}
            >
              1
          </button>
            <button
              onClick={() => selectDor(2)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 2 ? 0.5 : 1,
              }}
            >
              2
          </button>
            <button
              onClick={() => selectDor(3)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 3 ? 0.5 : 1,
              }}
            >
              3
          </button>
            <button
              onClick={() => selectDor(4)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 4 ? 0.5 : 1,
              }}
            >
              4
          </button>
            <button
              onClick={() => selectDor(5)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 5 ? 0.5 : 1,
              }}
            >
              5
          </button>
            <button
              onClick={() => selectDor(6)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 6 ? 0.5 : 1,
              }}
            >
              6
          </button>
            <button
              onClick={() => selectDor(7)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 7 ? 0.5 : 1,
              }}
            >
              7
          </button>
            <button
              onClick={() => selectDor(8)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 8 ? 0.5 : 1,
              }}
            >
              8
          </button>
            <button
              onClick={() => selectDor(9)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 9 ? 0.5 : 1,
              }}
            >
              9
          </button>
            <button
              onClick={() => selectDor(10)}
              className="blue-button"
              style={{
                padding: 2,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 35,
                backgroundColor: '#ec7063',
                opacity: dor < 10 ? 0.5 : 1,
              }}
            >
              10
          </button>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewFluxo === 1 && arrayfluxos.length > 0) {
    return (
      <div className="main fade-in">
        <Back></Back>
        <div id="fluxo" className="columngroup" style={{ marginTop: 80, height: '70vh' }}>
          <p className="title1" style={{ fontSize: 18 }}>
            O QUE VOCÊ ESTÁ SENTINDO?
        </p>
          <ShowFilter></ShowFilter>
          <div className="columngroup">
            <div id="fluxos" className="scroll" style={{ marginTop: 10, maxHeight: '50vh' }}>
              {arrayfluxos.map((item) => (
                <div
                  key={item.id}
                  className="row"
                  onClick={() => item.fluxo !== 'DOR EM OUTRA PARTE DO CORPO' ? selectFluxo(item) : scalaDor()}
                >
                  <button
                    className="blue-button"
                    style={{
                      padding: 10,
                      minHeight: 80,
                      width: 300,
                      /* LUCAS: vamos deixar este operador ternário que define a cor do botão conforme a gravidade
                      da queixa ou sintoma apresentado em standby. Poderá ser empregado futuramente...
                      backgroundColor: item.cor === 1 ? '#ec7063' : item.cor === 2 ? 'darkorange' : '#52be80',
                      */
                    }}
                  >
                    {item.fluxo}
                  </button>
                </div>
              ))}
            </div>
            <ShowEscalaDor></ShowEscalaDor>
          </div>
        </div>
      </div>
    );
  } else if (viewFluxo === 1 && arrayfluxos.length < 1) {
    return (
      <div className="main fade-in">
        <Back></Back>
        <div id="fluxo" className="columngroup" style={{ marginTop: 80, height: '70vh' }}>
          <p className="title1" style={{ fontSize: 18 }}>
            O QUE VOCÊ ESTÁ SENTINDO?
          </p>
          <ShowFilter></ShowFilter>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Fluxo;
