import axios from 'axios';
import React from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/pulsar.svg';
import '../design.css';

function Login() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewLogin = useSelector((state) => state.login);

  var usuario = null;
  var senha = null;

  useEffect(() => {
    if (viewLogin === 1) {
      // limpando os campos de login e senha.
      document.getElementById('inputUsuario').value = '';
      document.getElementById('inputSenha').value = '';
    }
  }, [viewLogin]);

  // função que busca um registro de login válido no servidor.
  const checkLogin = () => {
    usuario = document.getElementById('inputUsuario').value;
    senha = document.getElementById('inputSenha').value;
    var obj = {
      usuario: usuario.toUpperCase(),
      senha: senha.toUpperCase(),
    };
    axios
      // LUCAS: alterar a requisição abaixo conforme a tebela de usuários ou login que será criada.
      .post(html + "/login/'" + usuario + "'/'" + senha.toUpperCase() + "'", obj)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        // mapeando id, nome, idade e convênio do usuário.
        const loginid = x.map((item) => item.id);
        const loginnome = x.map((item) => item.nome);
        const loginidade = x.map((item) => item.dn);
        const loginconvenio = x.map((item) => item.convenio);
        setTimeout(function () {
          if (x.length > 0) {
            // capturando o primeiro nome do usuário (para exibição).
            var firstnameslicer = loginnome.split(' ');
            var firstname = firstnameslicer[0];
            changeState({
              type: 'WELCOME_ON',
              // payloads.
              payloadIdusuario: loginid,
              payloadNomeusuario: firstname,
            });
          } else {

          }
        }, 1000);
      });
  };

  // LUCAS: retirar essa função, que simula um falso login!
  const showMenu = () => {
    changeState({
      type: 'WELCOME_ON',
      // payloads.
      payloadIdusuario: 1,
      payloadNome: 'RODRIGO',
    });
  }

  // acessando a tela de cadastro.
  const telaCadastro = () => {
    changeState({
      type: 'CADASTRO_ON',
    });
  }

  // renderização do componente.
  if (viewLogin === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <img alt="" src={logo} style={{ height: 200 }}></img>
        <p className="title1" style={{ marginTop: 30 }}>
          G-HEALTH
        </p>
        <div className="columngroup">
          <input
            autoComplete="off"
            placeholder="LOGIN"
            className="input"
            type="text"
            id="inputUsuario"
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'LOGIN')}
            style={{
              marginTop: 20,
              marginBottom: 10,
              width: 200,
              height: 50,
            }}
          ></input>
          <input
            autoComplete="off"
            placeholder="SENHA"
            className="input"
            type="password"
            id="inputSenha"
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'SENHA')}
            style={{
              marginTop: 0,
              marginBottom: 0,
              width: 200,
              height: 50,
            }}
          ></input>
          <button
            className="blue-button"
            style={{
              marginTop: 30,
              width: 150,
              color: '#ffffff',
            }}
            //onClick={() => checkLogin()} // LUCAS: ativar esta linha, que executa corretamente o login!
            onClick={() => showMenu()} // LUCAS: eliminar esta linha, que simula um login!
          >
            ENTRAR
          </button>
          <label
            className="title2"
            style={{ fontSize: 12 }}
            onClick={() => telaCadastro()}
          >
            CRIAR CONTA
          </label>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Login;
