import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import check from '../images/check.jpg';
import '../design.css';
import moment, { locale } from 'moment';
import 'moment/locale/pt-br';
import QRCode from "react-qr-code";
import getDistance from 'geolib/es/getDistance';

function Sucesso() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewSucesso = useSelector((state) => state.sucesso);

  // constantes e variáveis associadas ao registro do usuário.
  const idusuario = useSelector((state) => state.idusuario);
  const nome = useSelector((state) => state.nome);
  const [dn, setdn] = useState('');
  const [convenio, setconvenio] = useState('');
  const [carteira, setcarteira] = useState('');

  // constantes associadas ao registro de pré-checkin.
  const [especialista, setespecialista] = useState('');
  const [fluxo, setfluxo] = useState('');
  const [escalador, setescalador] = useState('');
  const [cor, setcor] = useState('');
  const [horapedido, sethorapedido] = useState('');
  const [horachegada, sethorachegada] = useState('');
  const [autorizado, setautorizado] = useState('');

  // carregando o registro do usuário.
  const loadUsuario = () => {
    axios.get(html + "/usuario/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setdn(x.dn);
      setconvenio(x.convenio);
      setcarteira(x.carteira);
    });
  }

  // carregando o registro de pré-checkin.
  const loadPrecheckin = () => {
    axios.get(html + "/precheckin/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setespecialista(x.especialista);
      setfluxo(x.fluxo);
      setescalador(x.escalador);
      setcor(x.cor);
      sethorapedido(x.horapedido);
      sethorachegada(x.horachegda);
      sethorachegada(x.horachegada);
      setautorizado(x.autorizado);
    });
  }

  // definindo coordenadas da localização e distância do usuário.
  var usuarioLat = 0;
  var usuarioLong = 0;
  const [distancia, setdistancia] = useState(0)

  // coordenadas do hospital Santa Clara:
  var hospitalLat = -18.91996144949972;
  var hospitalLong = -48.28111991807318;

  useEffect(() => {
    if (viewSucesso === 1) {
      // carregando os resgistros do usuário e do pré-checkin.
      loadUsuario();
      loadPrecheckin();
      // obtendo dados de geolocalização do usuário.
      navigator.geolocation.getCurrentPosition(function (position) {
        usuarioLat = position.coords.latitude;
        usuarioLong = position.coords.longitude;
      });
      // calculando a distância entre o usuário e o hospital.
      var distance = getDistance(
        { latitude: hospitalLat, longitude: hospitalLong },
        { latitude: usuarioLat, longitude: usuarioLong }
      );
      console.log('DISTÂNCIA DO HOSPITAL: ' + distance / 1000 + ' km.');
      setdistancia(Math.round(distance / 1000));
      /* LUCAS: a aplicação precisa checar, periodicamente, se o pedido de pré-chekin foi autorizado
      pelo plano de saúde. Uma vez autorizado, o qrCode será gerado e o usuário estará habilitado a
      informar sua chegada ao hospital.*/
      setInterval(() => {
        loadPrecheckin()
        if (autorizado === 1) {
          setviewautorizacao(0); // oculta o componente que mostra a barra de progresso da autorização.
          setviewcheck(1); // exibe a tela "pre-chekin realizado com sucesso".
          setviewbutton(1); // exibe o botão para confirmação de chegada ao hospital.
        } else if (autorizado === 2) {
          setviewautorizacao(2); // oculta a barra de progresso e exibe a mensagem de não autorização.
          setviewcheck(1);
          setviewbutton(1);
        }
      }, 5000);
      // LUCAS: apagar a função abaixo (falsa autorização para demonstração).
      falsaAutorizacao();
    }
  }, [viewSucesso]);

  /* LUCAS: apagar o bloco abaixo (falso processo de autorização do pedido de atendimento). Este
  processo deverá ser realizado por uma secretária do hospital, que checará se o convênio autoriza o
  atendimento e clicará no botão correspondente dentro da tabela renderizada no componente Painel.*/
  const falsaAutorizacao = () => {
    setTimeout(() => {
      setpercent('100%');
      setTimeout(() => {
        setviewautorizacao(0);
        setviewcheck(1);
        setviewbutton(1);
      }, 1000);
    }, 5000);
    setautorizado(1);
    setviewautorizacao(1);
    setviewcheck(0);
    setviewbutton(0);
    setespecialista('CLÍNICA MÉDICA');
    sethorapedido(moment('14/12/2020 - 11:30', 'DD/MM/YYYY - HH:mm').format('DD/MM/YYYY - HH:mm'));
    sethorachegada(moment('14/12/2020 - 16:30', 'DD/MM/YYYY - HH:mm'));
  }

  /* função que atualiza o registro de pré-checkin, informando ao sistema que o paciente chegou
  no hospital para atendimento.*/
  var idpretriagem = 0;
  const chegou = () => {
    sethorachegada(moment());
    setviewcheck(0); // oculta a tela "pre-chekin realizado com sucesso".
    setviewbutton(0); // oculta o botão para confirmação de chegada.
    setviewqr(1); // exibe o qrCode.
    // atualizando o registro indicando-se que o paciente chegou.
    var obj = {
      idusuario: idusuario,
      nome: nome,
      convenio: convenio,
      carteira: carteira,
      fluxo: fluxo,
      escalador: escalador,
      cor: cor,
      especialista: especialista,
      horapedido: horapedido,
      horachegada: moment().format('DD/MM/YYYY hh:mm:ss'),
      chegou: 1, // 1 = chegou no hospital.
      autorizado: 1, // 1 = autorizdo, 2 = não autorizado.
    };
    axios.post(html + "/updateprecheckin/'" + idpretriagem + "'", obj);
  }

  // componente que renderiza a barra de progresso de autorização do pré-checkin.
  const [viewautorizacao, setviewautorizacao] = useState(1);
  const [percent, setpercent] = useState('50%');
  function ShowAutorizacao() {
    if (viewautorizacao === 1) {
      return (
        <div>
          <div className="title2">
            {'PROCESSANDO AUTORIZAÇÃO COM O SEU PLANO DE SAÚDE'}
          </div>
          <div className="linegroup">
            <button
              className="blue-button"
              style={{
                padding: 0,
                margin: 0,
                borderTopRightRadius: 0,
                borderBottomRightRadius: 0,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 50,
                backgroundColor: '#ec7063',
              }}
            >
            </button>
            <button
              className="blue-button"
              style={{
                padding: 0,
                margin: 0,
                borderTopLeftRadius: 0,
                borderBottomLeftRadius: 0,
                minHeight: 35,
                height: 35,
                minWidth: 35,
                width: 50,
                backgroundColor: percent === '100%' ? '#ec7063' : 'grey'
              }}
            >
            </button>
          </div>
          <div className="title2" style={{ fontSize: 18 }}>{percent}</div>
        </div>
      )
    } else if (viewautorizacao === 2) {
      return (
        <div>
          <div className="title2">
            {'SOLICITAÇÃO NÃO AUTORIZADA, FAVOR ENTRAR EM CONTATO COM SEU PLANO DE SAÚDE.'}
          </div>
        </div>
      )
    } else {
      return null;
    }
  }

  // componente que renderiza a mensagem "pré-checkin realizado com sucesso".
  const [viewcheck, setviewcheck] = useState(1);
  function ShowCheck() {
    if (viewcheck === 1) {
      return (
        <div className="fade-in">
          <div className="title2" style={{ maxWidth: 300 }}>
            {'SEU PRÉ-CHECK-IN FOI REGISTRADO COM SUCESSO ÀS ' + horapedido + '.'}
          </div>
          <img alt="" src={check} style={{ height: 100, margin: 5 }}></img>
        </div>
      )
    } else {
      return null;
    }
  }

  // componente que renderiza o botão para confirmação da chegada no hospital.
  const [viewbutton, setviewbutton] = useState(1);
  function ShowButton() {
    if (viewbutton === 1) {
      return (
        <div>
          <div className="title2" style={{ fontSize: 14, marginTop: 10, textAlign: 'center' }}>
            {'CONFIRME SUA CHEGADA PRESSIONANDO O BOTÃO ABAIXO. '}
          </div>
          <div className="linegroup">
            <button
              className="green-button"
              style={{
                marginTop: 5,
                width: 150,
                color: '#ffffff',
              }}
              onClick={() => chegou()}
            >
              CHEGUEI
            </button>
            <button
              className="red-button"
              style={{
                marginTop: 5,
                width: 150,
                color: '#ffffff',
              }}
              onClick={() => setmodalconfirma(1)}
            >
              CANCELAR
            </button>
          </div>
        </div>
      )
    } else {
      return null;
    }
  }

  // funções e componente que tratam da renderização do qrCode.
  const [qrcodevalue, setqrcodevalue] = useState(idpretriagem);
  const [viewqr, setviewqr] = useState(0);
  function QrCode() {
    if (viewqr === 1) {
      return (
        <div className="fade-in">
          <div className="title2" style={{ fontSize: 14, marginBottom: 10, maxWidth: 500 }}>{'OBRIGADO POR ESCOLHER NOSSO HOSPITAL! AGORA É SÓ APRESENTAR O QR CODE NO TOTEM E AGUARDAR A CHAMADA PARA ACOLHIMENTO.'}</div>
          <QRCode value={qrcodevalue} />
        </div>
      )
    } else {
      return null;
    }
  }

  // modal e funções que tratam do cancelamento do pré-checkin.
  const [modalconfirma, setmodalconfirma] = useState(0);
  function ModalConfirma() {
    if (modalconfirma === 1) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo" style={{ padding: 30 }}>
            <div className="title2" style={{ maxWidth: 300, textAlign: 'center' }}>  TEM CERTEZA QUE DESEJA CANCELAR SEU PRÉ-CHEKIN?</div>
            <div style={{ display: 'flex', flexDirection: 'row', marginTop: 5, maxWidth: 300 }}>
              <button
                className="red-button"
                style={{
                  width: 100,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => cancelaAtendimento()}
              >
                SIM
            </button>
              <button
                className="green-button"
                style={{
                  width: 100,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => setmodalconfirma(0)}
              >
                NÃO
            </button>
            </div>
          </div>
        </div>
      )
    } else {
      return null;
    }

  }
  const cancelaAtendimento = () => {
    setmodalconfirma(0);
    changeState({
      type: 'LOGIN_ON',
    });
  }

  // acessando o assistente virtual.
  const assistenteVirtual = () => {
    changeState({
      type: 'ASSISTENTE_ON',
      payloadIdusuario: idusuario,
      payloadNomeusuario: nome,
    });
  }

  // renderização do componente.
  if (viewSucesso === 1) {
    return (
      <div className="main fade-in">
        <ModalConfirma></ModalConfirma>
        <div className="columngroup" style={{ maxWidth: 800, textAlign: 'center' }}>
          <ShowAutorizacao></ShowAutorizacao>
          <ShowCheck></ShowCheck>
          <QrCode></QrCode>
          <div className="linegroup">
            <button
              className="widget"
              style={{
                flexDirection: 'column',
                width: 300,
                height: 200,
                backgroundColor: '#BFDBF7'
              }}>
              <div className="title2">
                {nome + ', SUA QUEIXA O DIRECIONOU PARA ATENDIMENTO NA SEGUINTE ESPECIALIDADE:'}
              </div>
              <div className="title2" style={{ color: 'green', fontSize: 18 }}>
                {especialista}
              </div>
              <div className="title2" style={{ color: 'red' }}>
                {'IMPORTANTE: A ESPECIALIDADE PODERÁ SER ALTERADA APÓS O ACOLHIMENTO.'}
              </div>
            </button>
            <button className="widget"
              style={{
                flexDirection: 'column',
                width: 300,
                height: 200,
                backgroundColor: '#BFDBF7'
              }}>
              <div className="title2">
                {'VOCÊ ESTÁ A ' + distancia + ' KM DO HOSPITAL SANTA CLARA. CASO DESEJE SER ATENDIDO, SEU PRÉ-CHECK-IN ' +
                  'TERÁ VALIDADE ATÉ ' + moment().add(distancia / 30, 'hours').format('DD/MM/YY - HH:mm') + '.'}
              </div>
              <div className="title2" style={{ color: 'red', fontSize: 18, marginBottom: 0 }}>
                {'PREVISÃO PARA ATENDIMENTO:'}
              </div>
              <div className="title2" style={{ color: 'red', fontSize: 18, marginTop: 0 }}>
                {horachegada !== '' ? moment(horachegada).add(1, 'hour').format('DD/MM/YYYY - HH:mm') : moment().add(distancia / 30 + 1, 'hours').format('DD/MM/YYYY - HH:mm')}
              </div>
            </button>
          </div>
          <button
            className="blue-button"
            style={{
              marginTop: 5,
              width: 150,
              color: '#ffffff',
            }}
            onClick={() => assistenteVirtual()}
          >
            ASSISTENTE VIRTUAL
            </button>
        </div>
        <ShowButton></ShowButton>
      </div>
    );
  } else {
    return null;
  }
}
export default Sucesso;
