import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import "react-datepicker/dist/react-datepicker.css";
import logo from '../images/logo-hospital-santa-clara.png';

function Welcome() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewWelcome = useSelector((state) => state.welcome);
  var idusuario = useSelector((state) => state.idusuario);
  var nome = useSelector((state) => state.nome);

  useEffect(() => {
    if (viewWelcome === 1) {
      navigator.geolocation.getCurrentPosition(function (position) {
        console.log("Latitude is :", position.coords.latitude);
        console.log("Longitude is :", position.coords.longitude);
      });
      setTimeout(() => {
        changeState({
          type: 'MENU_ON',
          // payloads.
          payloadIdusuario: idusuario,
          payloadNome: nome,
        })
      }, 3000);
    }
  }, [viewWelcome])

  // renderização do componente.
  if (viewWelcome === 1) {
    return (
      <div className="main fade-in">
        <div id="menu" className="columngroup" style={{ height: '100vh' }}>
          <div>
            <img alt="" src={logo} style={{ height: 100 }}></img>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Welcome;