import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import 'moment/locale/pt-br';

function Painel() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const viewPainel = useSelector((state) => state.painel);
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);

  var pseudolista = [
    {
      paciente: "RODRIGO CARVALHO LESSA",
      procedimento: '01010128',
      data: '15/12/2020 - 08:00',
      convenio: 'BRADESCO',
      plano: 'NACIONAL',
      carteira: '1234567890056',
    },
    {
      paciente: "MARCOS ANTÔNIO LACROSSE",
      procedimento: '01010133',
      data: '15/12/2020 - 12:00',
      convenio: 'UNIMED',
      plano: 'NACIONAL',
      carteira: '1234562281754',
    },
  ];

  useEffect(() => {
    if (viewPainel === 1) {
      loadPainel();
      // carregamento periódico da lista de pré-checkins aguardando autorização.
      setInterval(() => {
        loadPainel();
      }, 10000);
    }
  }, [viewPainel]);

  // carregado a lista de solicitações de pré-chekin.
  const [autorizacao, setautorizacao] = useState(pseudolista);
  const loadPainel = () => {
    axios.get(html + "/painelautorizacao").then((response) => {
      setautorizacao(response.data);
    });
  }

  // modal para confirmar ou negar uma autorização.
  const [precheckin, setprecheckin] = useState(0);
  const viewModal = (number, value) => {
    setprecheckin(value);
    if (number === 1) {
      setmodalautorizacao(1);
    } else if (number === 2) {
      setmodalautorizacao(2);
    } else {
      setmodalautorizacao(0);
    }
  }
  const [modalautorizacao, setmodalautorizacao] = useState(0);
  function ModalAutorizacao() {
    if (modalautorizacao === 1) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo" style={{ padding: 30 }}>
            <div className="title2">  NEGAR AUTORIZAÇÃO DO ATENDIMENTO?</div>
            <div className="linegroup" style={{ width: 400, marginTop: 10 }}>
              <button
                className="green-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => cancelaAutorizacao()}
              >
                NÃO, VOLTAR AO PAINEL
            </button>
              <button
                className="red-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => setmodalautorizacao(0)}
              >
                SIM, CANCELAR
            </button>
            </div>
          </div>
        </div>
      )
    } else if (modalautorizacao === 2) {
      return (
        <div className="janela-fundo" style={{ position: 'absolute' }}>
          <div className="janela-conteudo" style={{ padding: 30 }}>
            <div className="title2">  CONFIRMAR AUTORIZAÇÃO DO ATENDIMENTO?</div>
            <div className="linegroup" style={{ width: 400, marginTop: 10 }}>
              <button
                className="red-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => setmodalautorizacao(0)}
              >
                NÃO, VOLTAR AO PAINEL
            </button>
              <button
                className="green-button"
                style={{
                  width: 200,
                  padding: 10,
                  color: '#ffffff',
                }}
                onClick={() => confirmaAutorizacao()}
              >
                SIM, AUTORIZAR
            </button>
            </div>
          </div>
        </div>
      )
    } else {
      return null;
    }

  }
  const confirmaAutorizacao = () => {
    // LUCAS: fetch para atualizar o registro de pré-checkin como autorizado.
    axios.get(html + "/deleteprecheckin").then((response) => {
      setTimeout(() => {
        loadPainel(); // atualização da lista, com a saída do registro cancelado ou autorizado.
      }, 1000);
    });
    setmodalautorizacao(0);
  }
  const cancelaAutorizacao = () => {
    setmodalautorizacao(0);
  }

  // renderização do componente.
  if (viewPainel === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <ModalAutorizacao></ModalAutorizacao>
        <div className="columngroup" style={{ textAlign: 'center' }}>
          <div className="title1">
            {'PAINEL DE AUTORIZAÇÃO'}
          </div>
          <div id="cabeçalho" className="scrollheader" style={{ margin: 0 }}>
            <div className="row" style={{ height: 40 }}>
              <button
                className="blue-button"
                style={{
                  width: 300,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                PACIENTE
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                PROCEDIMENTO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                DATA/HORA
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                CONVENIO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                PLANO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                CARTEIRA
              </button>
              <button
                className="red-button"
                style={{
                  width: 50,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                NEG
                </button>
              <button
                className="green-button"
                style={{
                  width: 50,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                AUT
                </button>
            </div>
          </div>
          <div id="painel de autorizações" className="scroll" style={{ marginTop: 10, height: '70vh' }}>
            {autorizacao.map((item) => (
              <div
                key={item.id}
                className="row"
              >
                <button
                  className="blue-button"
                  style={{
                    width: 300,
                  }}
                >
                  {item.paciente}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.procedimento}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.data}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.convenio}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.plano}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.carteira}
                </button>
                <button
                  className="red-button"
                  onClick={() => viewModal(1, item.id)}
                  style={{
                    width: 50,
                  }}
                >
                  NEG
                </button>
                <button
                  className="green-button"
                  onClick={() => viewModal(2, item.id)}
                  style={{
                    width: 50,
                  }}
                >
                  AUT
                </button>
              </div>
            ))}
          </div>
        </div>
      </div >
    );
  } else {
    return null;
  }
}
export default Painel;