import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import "react-datepicker/dist/react-datepicker.css";
import voltar from '../images/voltar.svg';
import axios from 'axios';

function Menu() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewMenu = useSelector((state) => state.menu);

  // constantes e variáveis associadas ao registro do usuário.
  const idusuario = useSelector((state) => state.idusuario);
  const nome = useSelector((state) => state.nome);

  /* constante que indica se o usuário já tem um pre-checkin registrado ou não. Esta informação é
  fundamental para que os botões do menu sejam apropriadamente exibidos.*/
  const [checktriagem, setchecktriagem] = useState(0);

  useEffect(() => {
    if (viewMenu === 1) {
      // checando se o usuário já está em atendimento.
      axios.get(html + "/checkpretriagem/'" + idusuario + "'").then((response) => {
        var x = [0, 1];
        x = response.data;
        if (x.length > 0) { // já existe um registro de pré-checkin.
          setchecktriagem(1);
        } else { // não há registro de pré-chekin.
          setchecktriagem(0);
        }
      });
      setchecktriagem(0); // LUCAS: retirar esta linha de código (usada para demonstrar o fluxo de telas ao cliente).
    }
  }, [viewMenu])

  // retornar ao login.
  const back = () => {
    changeState({
      type: 'LOGIN_ON',
      // payloads.
      payloadIdusuario: '',
      payloadNome: 0,
    })
  }
  function Back() {
    return (
      <div
        style={{
          position: 'absolute',
          top: 10, left: 10,
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'flex-start',
        }}>
        <button
          className="blue-button"
          style={{
            margin: 10,
            borderRadius: 50,
            opacity: 0.8,
            minHeight: 30,
            minWidth: 30,
            height: 30,
            width: 30,
          }}
          onClick={() => back()}
          title="VOLTAR"
        >
          <img
            alt=""
            src={voltar}
            style={{
              margin: 10,
              height: 20,
              width: 20,
            }}
          ></img>
        </button>
        <div className="title2"> {'OLÁ ' + nome}</div>
      </div>
    )
  }

  // selecionando a opção do menu "QUERO SER ATENDIDO".
  const selectStartAtendimento = () => {
    changeState({
      type: 'FLUXO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNome: nome,
    });
  }

  // selecionando a opção do menu "ACOMPANHAR ATENDIMENTO".
  const selectStatusAtendimento = () => {
    changeState({
      type: 'SUCESSO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNome: nome,
    });
  }

  // selecionando a opção do menu "ASSISTENTE VIRTUAL".
  const selectVirtualHelper = () => {
    changeState({
      type: 'ASSISTENTE_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNome: nome,
    });
  }

  function Atendimento() {
    if (checktriagem === 1) {
      return (
        <div
          className="title2"
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
          }}>
          {'VOCÊ JÁ ESTÁ AGUARDANDO ATENDIMENTO NO HOSPITAL.'}
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewMenu === 1) {
    return (
      <div className="main fade-in">
        <Back></Back>
        <div id="menu" className="columngroup" style={{ marginTop: 80, height: '70vh  ' }}>
          <p className="title1">
            PRÉ-CHECK IN
        </p>
          <Atendimento></Atendimento>
          <div className="linegroup">
            <button
              disabled={checktriagem === 0 ? false : true}
              className="blue-button"
              style={{
                color: '#ffffff',
                margin: 10,
                padding: 20,
                borderRadius: 5,
                width: 200,
                height: 200,
                fontSize: 18,
                opacity: checktriagem === 0 ? 1 : 0.5,
              }}
              onClick={() => selectStartAtendimento()}
            >
              QUERO SER ATENDIDO
            </button>
            <button
              disabled={checktriagem === 1 ? false : true}
              className="blue-button"
              style={{
                color: '#ffffff',
                margin: 10,
                padding: 20,
                borderRadius: 5,
                width: 200,
                height: 200,
                fontSize: 18,
                opacity: checktriagem === 1 ? 1 : 0.5,
              }}
              onClick={() => selectStatusAtendimento()}
            >
              ACOMPANHAR ATENDIMENTO
            </button>
            <button
              disabled={checktriagem === 1 ? false : true}
              className="blue-button"
              style={{
                color: '#ffffff',
                margin: 10,
                padding: 20,
                borderRadius: 5,
                width: 200,
                height: 200,
                fontSize: 18,
                opacity: checktriagem === 1 ? 1 : 0.5,
              }}
              onClick={() => selectVirtualHelper()}
            >
              ASSISTENTE VIRTUAL
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Menu;
