import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import moment, { locale } from 'moment';
import 'moment/locale/pt-br';

function Prescricao() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewPrescricao = useSelector((state) => state.prescricao);
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);
  const especialistausuario = useSelector((state) => state.especialistausuario);
  const horachegada = useSelector((state) => state.horachegada);

  var pseudolista = [
    {
      medicacao: "DIPIRONA",
      quantidade: '1AMP',
      via: 'IV',
      ultima: '18:00',
      proxima: '00:00',
    },
    {
      medicacao: "METOCLOPRAMIDA",
      quantidade: '1AMP',
      via: 'IV',
      ultima: '14:00',
      proxima: '22:00',
    },
  ];

  // carregamento da janela.
  useEffect(() => {
    if (viewPrescricao === 1) {
      setInterval(() => {
        loadPrescricao();
      }, 1000);
    }
  }, [viewPrescricao]);

  const [prescricao, setprescricao] = useState([]);
  const loadPrescricao = () => {
    axios.get(html + "/exames").then((response) => {
      setprescricao(response.data);
    });
    setprescricao(pseudolista);
  }

  const voltar = () => {
    changeState({
      type: 'ASSISTENTE_ON',
      payloadIdusuario: idusuario,
      payloadNomeusuario: nomeusuario,
      payloadEspecialistausuario: especialistausuario,
      payloadHorachegada: horachegada,
    });
  }

  // renderização do componente.
  if (viewPrescricao === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <div className="columngroup" style={{ textAlign: 'center' }}>
          <div className="title1">
            {'PAINEL DE PRESCRIÇÃO'}
          </div>
          <div id="cabeçalho" className="scrollheader" style={{ margin: 0 }}>
            <div className="row" style={{ height: 40 }}>
              <button
                className="blue-button"
                style={{
                  width: 300,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                MEDICAMENTO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                QUANTIDADE
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                VIA
              </button>
              <button
                className="blue-button"
                style={{
                  width: 200,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                ÚLTIMA DOSE
              </button>
              <button
                className="blue-button"
                style={{
                  width: 200,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                PRÓXIMA DOSE
              </button>
            </div>
          </div>
          <div id="painel de exames" className="scroll" style={{ marginTop: 10, height: '70vh' }}>
            {prescricao.map((item) => (
              <div
                key={item.id}
                className="row"
              >
                <button
                  className="blue-button"
                  style={{
                    width: 300,
                  }}
                >
                  {item.medicacao}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.quantidade}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                  }}
                >
                  {item.via}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 200,
                  }}
                >
                  {item.ultima}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 200,
                  }}
                >
                  {item.proxima}
                </button>
              </div>
            ))}
          </div>
          <button
            onClick={() => voltar()}
            className="blue-button"
            style={{
              padding: 10,
              marginTop: 10,
            }}
          >
            {'VOLTAR'}
          </button>
        </div>
      </div >
    );
  } else {
    return null;
  }
}
export default Prescricao;