import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import bot from '../images/bot.gif';
import '../design.css';
import moment, { locale } from 'moment';
import 'moment/locale/pt-br';
import axios from 'axios';

function Assistente() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewAssistente = useSelector((state) => state.assistente);

  // constantes e variáveis associadas ao registro do usuário.
  const idusuario = useSelector((state) => state.idusuario);
  const nome = useSelector((state) => state.nome);
  const [dn, setdn] = useState('');
  const [convenio, setconvenio] = useState('');
  const [carteira, setcarteira] = useState('');

  // constantes associadas ao registro de pré-checkin.
  const [especialista, setespecialista] = useState('');
  const [fluxo, setfluxo] = useState('');
  const [escalador, setescalador] = useState('');
  const [cor, setcor] = useState('');
  const [horapedido, sethorapedido] = useState('');
  const [horachegada, sethorachegada] = useState('');
  const [autorizado, setautorizado] = useState('');

  // carregando o registro do usuário.
  const loadUsuario = () => {
    axios.get(html + "/usuario/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setdn(x.dn);
      setconvenio(x.convenio);
      setcarteira(x.carteira);
    });
  }

  // carregando o registro de pré-checkin.
  const loadPrecheckin = () => {
    axios.get(html + "/precheckin/'" + idusuario + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setespecialista(x.especialista);
      setfluxo(x.fluxo);
      setescalador(x.escalador);
      setcor(x.cor);
      sethorapedido(x.horapedido);
      sethorachegada(x.horachegda);
      setautorizado(x.autorizado);
    });
    setespecialista('CLÍNICA MÉDICA'); // LUCAS: retirar esta linha, usada na demonstração.
    sethorachegada(moment('14/12/2020 - 17:45', 'DD/MM/YYYY - HH:mm')); // LUCAS: retirar esta linha, usada na demonstração.
  }

  useEffect(() => {
    if (viewAssistente === 1) {
      loadUsuario();
      loadPrecheckin();
    }
  }, [viewAssistente]);

  const prescricao = () => {
    changeState({
      type: 'PRESCRICAO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: nome,
    });
  }

  const exames = () => {
    changeState({
      type: 'EXAMES_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: nome,
    });
  }

  // renderização do componente.
  if (viewAssistente === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'flex-start' }}>
        <img
          alt=""
          src={bot}
          style={{
            margin: 0,
            padding: 0,
            height: 300,
          }}
        ></img>
        <div className="title2" style={{ fontSize: 16, margin: 0, marginLeft: 20, marginRight: 20 }}>{'OLÁ, ' + nome + ', SOU SEU ASSISTENTE VIRTUAL.'}</div>
        <div className="title2" style={{ textAlign: 'center', marginBottom: 0 }}>{'VEJA ABAIXO ALGUMAS INFORMAÇÕES SOBRE SEU ATENDIMENTO.'}</div>
        <div className="linegroup">
          <button
            className="yellow-button"
            style={{
              padding: 10,
              marginTop: 30,
              width: 250,
              height: 130,
              color: '#ffffff',
            }}
          >
            <div style={{ fontSize: 16 }}>{'TEMPO GERAL DE ESPERA PARA ATENDIMENTO:'}</div>
            <div style={{ marginTop: 10, fontSize: 30 }}>{'00:55' /* onde pegaremos essa informação no MV? */}</div>
          </button>
          <button
            className="yellow-button"
            style={{
              padding: 10,
              marginTop: 30,
              width: 250,
              height: 130,
              color: '#ffffff',
            }}
          >
            <div style={{ fontSize: 16 }}>{'TEMPO DE ESPERA PARA ' + especialista + ':'}</div>
            <div style={{ marginTop: 10, fontSize: 30 }}>{'00:36' /* onde pegaremos essa informação no MV? */}</div>
          </button>
          <button
            className="yellow-button"
            style={{
              padding: 10,
              marginTop: 30,
              width: 250,
              height: 130,
              color: '#ffffff',
            }}
          >
            <div style={{ fontSize: 16 }}>{'HORA PREVISTA PARA SEU ATENDIMENTO: '}</div>
            <div style={{ marginTop: 10, fontSize: 30 }}>{moment(horachegada).add(1, 'hour').format('HH:mm')}</div>
          </button>
        </div>
        <div className="linegroup">
          <button
            className="blue-button"
            style={{
              padding: 10,
              marginTop: 10,
              width: 200,
              color: '#ffffff',
            }}
            onClick={() => prescricao()}
          >
            MINHA PRESCRIÇÃO
        </button>
          <button
            className="blue-button"
            style={{
              padding: 10,
              marginTop: 10,
              width: 200,
              color: '#ffffff',
            }}
            onClick={() => exames()}
          >
            MEUS EXAMES
        </button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Assistente;