import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import '../design.css';
import moment, { locale } from 'moment';
import 'moment/locale/pt-br';

function Exames() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewExames = useSelector((state) => state.exames);
  const idusuario = useSelector((state) => state.idusuario);
  const nomeusuario = useSelector((state) => state.nomeusuario);
  const especialistausuario = useSelector((state) => state.especialistausuario);
  const horachegada = useSelector((state) => state.horachegada);

  var pseudolista = [
    {
      exame: "HEMOGRAMA",
      coleta: '16/12/2020 - 15h37',
      resultado: '16/12/2020 - 17h53',
      valor: 'HGB 12, HTO 38, PLAQ 343.000, GL 6800 (BAST 0%, SEGM 65%, LINF 12%)',
      status: 'PRONTO',
    },
    {
      exame: "TROPONINA",
      coleta: '16/12/2020 - 15h37',
      resultado: '16/12/2020 - 15h50',
      valor: '125',
      status: 'PRONTO',
    },
    {
      exame: "URINA ROTINA",
      coleta: '16/12/2020 - 15h45',
      resultado: '',
      valor: '',
      status: 'PENDENTE',
    },
  ];

  // carregamento da janela.
  useEffect(() => {
    if (viewExames === 1) {
      setInterval(() => {
        loadExames();
      }, 1000);
    }
  }, [viewExames]);

  const [exames, setexames] = useState([]);
  const loadExames = () => {
    axios.get(html + "/exames").then((response) => {
      setexames(response.data);
    });
    setexames(pseudolista);
  }

  const voltar = () => {
    changeState({
      type: 'ASSISTENTE_ON',
      payloadIdusuario: idusuario,
      payloadNomeusuario: nomeusuario,
      payloadEspecialistausuario: especialistausuario,
      payloadHorachegada: horachegada,
    });
  }

  // renderização do componente.
  if (viewExames === 1) {
    return (
      <div className="main fade-in" style={{ justifyContent: 'center' }}>
        <div className="columngroup" style={{ textAlign: 'center' }}>
          <div className="title1">
            {'PAINEL DE EXAMES'}
          </div>
          <div id="cabeçalho" className="scrollheader" style={{ margin: 0 }}>
            <div className="row" style={{ height: 40 }}>
              <button
                className="blue-button"
                style={{
                  width: 300,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                EXAME
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                COLETA
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                RESULTADO
              </button>
              <button
                className="blue-button"
                style={{
                  width: 500,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                VALOR
              </button>
              <button
                className="blue-button"
                style={{
                  width: 150,
                  height: 30,
                  color: '#1f7a8c',
                  backgroundColor: 'transparent',
                }}
              >
                STATUS
              </button>
            </div>
          </div>
          <div id="painel de exames" className="scroll" style={{ marginTop: 10, height: '70vh' }}>
            {exames.map((item) => (
              <div
                key={item.id}
                className="row"
              >
                <button
                  className="blue-button"
                  style={{
                    width: 300,
                    backgroundColor: item.status === 'PENDENTE' ? '#ec7063' : '#52be80',
                  }}
                >
                  {item.exame}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.status === 'PENDENTE' ? '#ec7063' : '#52be80',
                  }}
                >
                  {item.coleta}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.status === 'PENDENTE' ? '#ec7063' : '#52be80',
                  }}
                >
                  {item.resultado}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 500,
                    padding: 10,
                    justifyContent: 'left',
                    textAlign: 'left',
                    backgroundColor: item.status === 'PENDENTE' ? '#ec7063' : '#52be80',
                  }}
                >
                  {item.valor}
                </button>
                <button
                  className="blue-button"
                  style={{
                    width: 150,
                    backgroundColor: item.status === 'PENDENTE' ? '#ec7063' : '#52be80',
                  }}
                >
                  {item.status}
                </button>
              </div>
            ))}
          </div>
          <button
            onClick={() => voltar()}
            className="blue-button"
            style={{
              padding: 10,
              marginTop: 10,
            }}
          >
            {'VOLTAR'}
          </button>
        </div>
      </div >
    );
  } else {
    return null;
  }
}
export default Exames;